import parser from 'fast-xml-parser'
import { generate } from 'astring'
import { x2jParserOptions } from '../config/xmlParser'
import astringOptions from '../config/astring'

const ast2src = (input, lang) => {
  try {
    const ast = lang === 'xml' ? parser.parse(input, x2jParserOptions).root : JSON.parse(input)
    const srcLang = ast.language
    const src = (() => {
      switch (srcLang) {
        case 'javascript':
          return generate(ast, astringOptions)
        default:
          throw new Error('Can\'t generate src')
      }
    })()
    return { [srcLang]: src }
  } catch (e) {
    return { error: e.message }
  }
}

export default ast2src
